<template>
  <v-dialog :value="value" @input="$emit('input')" max-width="1000px">
    <v-card>
      <v-toolbar color="secondary" flat dark dense extense>
        <v-toolbar-title> Vista previa </v-toolbar-title>
      </v-toolbar>
      <v-card-text>
        <div id="factura">
          <header>
            <div id="logo">
              <img
                src="data:image/png;base64,
"
                id="imagen"
              >
            </div>
            <div id="datos">
              <p id="encabezado">
                <b>Company Name</b>
                <br>Mcal. Lopez 1368 c/ Sen. Long - Asuncion, Paraguay
                <br>Telefonos: 021-488-899 / (+595) 981-300-250
                <br>Email: company@info.com.py
              </p>
            </div>
            <div id="fact">
              <p>
                <b>Comprobante Nro.:</b>
                {{venta.nroComprobante}}
                <br>
                <b>Fecha:</b>
                {{columnDate(venta.fechaHora)}}
                <br>
                <b>Usuario:</b>
                {{venta.nombreUsuario}}
              </p>
            </div>
          </header>
          <br>
          <section>
            <div>
              <table id="facliente">
                <tbody>
                  <tr>
                    <td id="cliente">
                      <strong>Cliente: </strong>
                      {{venta.nombreCliente}}
                      <br>
                      <strong>Ruc: </strong> {{venta.nroDocumentoCliente}}
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </section>
          
          <section>
            <div>
              <table id="facarticulo">
                <thead>
                  <tr id="fa">
                    <th>CANT</th>
                    <th>DESCRIPCION</th>
                    <th>PRECIO UNIT</th>
                    <th>DESC.</th>
                    <th>PRECIO TOTAL</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="detalle in venta.detalles" :key="detalle.id">
                    <td style="text-align:center;">{{detalle.cantidad}}</td>
                    <td>{{detalle.nombreArticulo}}</td>
                    <td style="text-align:right;">{{columnMoney(detalle.precio)}}</td>
                    <td style="text-align:right;">{{columnMoney(detalle.descuento)}}</td>
                    <td
                      style="text-align:right;"
                    >{{columnMoney(calcularSubtotal(detalle)) }}</td>
                  </tr>
                </tbody>
              </table>
              <div id="totales">
                    <tr>
                      <td width="120px">
                        <b>Total Parcial:</b>
                      </td>
                      <td class="text-xs-right">{{columnMoney(calcularTotal() - calcularImpuesto())}}</td>
                    </tr>
                    <tr>
                      <td>
                        <b>Total Impuesto:</b>
                      </td>
                      <td class="text-xs-right">{{columnMoney(calcularImpuesto())}}</td>
                    </tr>
                    <tr>
                      <td>
                        <b>Total Neto:</b>
                      </td>
                      <td class="text-xs-right">{{columnMoney(calcularTotal())}}</td>
                    </tr>
                  </div>
            </div>
          </section>
          <br><br><br>
          <footer>
            <div id="gracias">
              <p>
                <b>Gracias por su preferencia!</b>
                <br>
                <b>Obs:</b> Este documento virtual es solo un comprobante de su compra, para cuestiones tributarias solicite una factura impresa.
              </p>
            </div>
          </footer>
        </div>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="secondary" flat @click.native="cerrar">Cancelar</v-btn>
        <v-btn color="primary" flat @click="crearPDF">
          Descargar
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import columnasMixin from "../Mixins/columnasMixin.js";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
export default {
  mixins: [columnasMixin],
  name: "ComprobanteVenta",
  props: {
    value: {
      type: Boolean
    },
    venta: {
      type: Object
    }
  },
  data() {
    return {};
  },
  methods: {
    crearPDF(){
      var quotes = document.getElementById("factura");
      html2canvas(quotes).then(function(canvas){
        var imgData = canvas.toDataURL('image/png');
        var imgWidth = 210;
        var pageHeight = 295;
        var imgHeight = canvas.height * imgWidth / canvas.width;
        var heightLeft = imgHeight;
        var doc = new jsPDF('p','mm','a4');
        var position = 0

        doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
        doc.save('comprobante-venta.pdf');
      });
    },
    calcularSubtotal(detalle) {
      return detalle.cantidad * detalle.precio - detalle.descuento;
    },
    calcularTotal() {
      var sum = 0;
      this.venta.detalles.forEach(
        d => (sum += d.cantidad * d.precio - d.descuento)
      );
      this.venta.total = sum;
      return sum;
    },
    calcularImpuesto() {
      var impuesto = this.venta.impuesto ? parseInt(this.venta.impuesto) : null;
      return (this.venta.total * impuesto) / (100 + impuesto);
    },
    cerrar() {
      this.$emit("input");
      console.log(this.venta);
    }
  },
  computed: {}
};
</script>
<style>
#factura {
  padding: 20px;
  font-family: Arial, sans-serif;
  font-size: 16px;
}

#logo {
  float: left;
  margin-left: 2%;
  margin-right: 2%;
}
#imagen {
  width: 100px;
}

#fact {
  float: right;
}

#datos {
  float: left;
  margin-top: 0%;
  margin-left: 2%;
  margin-right: 2%;
  /*text-align: justify;*/
}

#encabezado {
  text-align: left;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 16px;
}

section {
  clear: left;
}

#cliente {
  text-align: left;
}

#facliente {
  width: 40%;
  border-collapse: collapse;
  border-spacing: 0;
  margin-bottom: 15px;
}

#fa {
  color: #ffffff;
  font-size: 14px;
}

#facarticulo {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  padding: 20px;
  margin-bottom: 15px;
}

#facarticulo thead {
  padding: 20px;
  background: #2183e3;
  text-align: center;
  border-bottom: 1px solid #ffffff;
}

#gracias {
  text-align: center;
}

#totales{
  float: right;
}

</style>
